<template>
  <div class="img_view" v-if="imgShow">
    <div class="cover" @click="close()"></div>
    <div class="img">
      <img :src="IMG_URL" alt />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    IMG_URL: String
  },
  data() {
    return {
      imgShow: false
    };
  },
  methods: {
    close() {
      let that = this;
      that.imgShow = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.img_view {
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  .cover {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .img {
    width: 80%;
    img {
      width: 100%;
      animation: imgMove 0.3s ease both 1;
    }
  }
}
@keyframes imgMove {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
</style>


