<template>
  <div class="content">
    <div class="left_plan">
      <div class="plan">
        <span class="p"></span>
      </div>
    </div>
    <div
      class="pt_thing"
      v-for="(i,index) in list"
      :key="index"
      :style="`animation-delay:${index*0.2}s;`"
    >
      <p class="time">{{i.time}}</p>
