      <div class="img_box" v-if="i.imgList && i.imgList.length">
        <div class="img" v-for="(j,index) in i.imgList" :key="index">
          <img :src="j.url" alt @click="toImgView" />
        </div>
      </div>
    </div>
    <img-view ref="imgView" :IMG_URL="imgUrl"></img-view>
  </div>
</template>

